import { HIDE_PRODUCT_DETAILS, SHOW_PRODUCT_DETAILS} from './all-action.types';


export const showProductDetails = (val: string) => {
    return {
        type: SHOW_PRODUCT_DETAILS,
        data: val
    };
};
export const hideProductDetails = () => {
    return {
        type: HIDE_PRODUCT_DETAILS,
        data: null
    };
};
