import React, {Component} from 'react';
import {createStyles, Theme, useTheme, withStyles, WithStyles} from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import {connect} from 'react-redux';
import compose from 'recompose/compose';
import {decreaseCounter, increaseCounter} from '../../redux/Actions/counter.actions';
import {hideProductDetails} from '../../redux/Actions/product-details.actions';
import {Box, CardMedia, Grid} from '@material-ui/core';


const styles = (theme: Theme) =>
    createStyles({
        root: {
            margin: 0,
            padding: theme.spacing(2),
        },
        closeButton: {
            position: 'absolute',
            right: theme.spacing(1),
            top: theme.spacing(1),
            color: theme.palette.grey[500],
        },
    });

export interface DialogTitleProps extends WithStyles<typeof styles> {
    id: string;
    children: React.ReactNode;
    onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
    const {children, classes, onClose, ...other} = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon/>
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const DialogContent = withStyles((theme: Theme) => ({
    root: {
        padding: theme.spacing(2),
    },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(1),
    },
}))(MuiDialogActions);

class ProductDetailsComponent extends Component<any, any> {
    componentWillUpdate(nextProps: Readonly<any>) {
        // this.setState({
        //     open: nextProps.showProduct
        // })
    }

    // componentWillUpdate(newProps:any){
    //     if (newProps.count!== this.props.count){
    //         console.log('finally')
    //     }
    // }
    state = {
        open: false
    }

    render() {

        const handleClickOpen = () => {
            this.setState({
                open: true
            })
        };
        const handleClose = () => {
            this.props.hideProductDetails()
        };
        const product = this.props.productData;
        const open = this.props.showProduct;
        return (
            <React.Fragment>
                {open ?
                    <div>
                        <Dialog fullWidth={true}
                                maxWidth={'md'} onClose={handleClose} aria-labelledby="customized-dialog-title"
                                open={open}>
                            <DialogContent dividers>
                                <Grid container>
                                    <Grid item lg={6}>
                                        <CardMedia
                                            component="img"
                                            height="350"
                                            width={200}
                                            alt="Contemplative Reptile"
                                            image={product.img}
                                            title="Contemplative Reptile"
                                        />
                                    </Grid>
                                    <Grid item lg={6}>
                                        <Box ml={4}>
                                            <Typography variant={'h6'}>
                                                {product.name}
                                            </Typography>
                                            <Box mb={2}>
                                                <p>{product.quantity} pc(s)</p>
                                            </Box>
                                            <Typography gutterBottom>
                                                {product.desc}
                                            </Typography>
                                            <Box mt={4}>
                                                <Grid item lg={12}>

                                                    <Box component="h5" display="inline">
                                                        <Typography className={'product-details-price'} color="secondary"
                                                                variant={'h5'}>
                                                          ₹ {product.price}
                                                        </Typography>
                                                    </Box>
                                                    <Box component="h5" display="inline">
                                                        <Typography
                                                                    color="inherit"
                                                                    variant={'h6'}>
                                                            <del> ₹ {Number(product.price) + Number(product.discount)}</del>
                                                        </Typography>
                                                    </Box>
                                                </Grid>
                                            </Box>
                                            <Box mt={4}>
                                                <Grid item lg={12}>
                                                    <Button size="small"  variant="outlined" color="inherit" >
                                                    Add to cart
                                                </Button>
                                                </Grid>
                                            </Box>
                                        </Box>
                                    </Grid>
                                </Grid>
                            </DialogContent>
                        </Dialog>
                    </div>
                    : null}
            </React.Fragment>
        );
    }
}

const mapStateToProps = (state: any) => {
    return {
        showProduct: state.productDetails.showProduct,
        productData: state.productDetails.productData
    }
}
const mapDispatchToProps = (dispatch: any) => {
    return {
        hideProductDetails: () => dispatch(hideProductDetails()),
    }
}
export default compose(
    withStyles(styles, {withTheme: true}),
    connect(mapStateToProps, mapDispatchToProps),
)(ProductDetailsComponent);
