import React, {useState} from 'react';
import {Box, Button, FormControl, Grid, TextField, Typography,} from '@material-ui/core';
import {makeStyles} from '@material-ui/core/styles';
import CheckoutAddressListComponent from './Checkout-address-list.component';
const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    margin: {
        margin: theme.spacing(1),
    },
    withoutLabel: {
        marginTop: theme.spacing(3),
    },
    textField: {
        width: '25ch',
    },
}));
export default function CheckOutAddressComponent(props: any) {
    const classes = useStyles();
    const [ addressList, setAddressList ] = useState(true)
    const showForm = () => {
        setAddressList(false)
    }
    const hideForm = () => {
        setAddressList(true)
    }
    return (
        <React.Fragment>
            {addressList ?
                <CheckoutAddressListComponent nextStep={() => {props.nextStep()}} showAddressForm={showForm}/>
                :
                <div>
                    <Box mt={3} pr={3} mr={5}>
                        <Typography variant="h6">
                            Add New Address
                        </Typography>
                    </Box>
                <Grid container lg={8}>
                    <Grid item lg={6}>
                        <Box mb={3} mr={3}>
                            <FormControl fullWidth className={classes.margin}>
                                <TextField color="secondary" id="standard-basic" label="Full Name"/>
                            </FormControl>
                        </Box>
                        <Box mb={3} mr={3}>
                            <FormControl fullWidth className={classes.margin}>
                                <TextField color="secondary" id="standard-basic" label="Address 1"/>
                            </FormControl>
                        </Box>
                        <Grid container>
                            <Grid item lg={6}>
                                <Box mb={3} mr={3}>
                                    <FormControl fullWidth className={classes.margin}>
                                        <TextField color="secondary" id="standard-basic" label="City"/>
                                    </FormControl>
                                </Box>
                            </Grid>
                            <Grid item lg={6}>
                                <Box mb={3} mr={3}>
                                    <FormControl fullWidth className={classes.margin}>
                                        <TextField color="secondary" id="standard-basic" label="Pin Code"/>
                                    </FormControl>
                                </Box>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item lg={6}>
                        <Box mb={3} mr={3}>
                            <FormControl fullWidth className={classes.margin}>
                                <TextField color="secondary" id="standard-basic" label="Mobile Number"/>
                            </FormControl>
                        </Box>
                        <Box mb={3} mr={3}>
                            <FormControl fullWidth className={classes.margin}>
                                <TextField color="secondary" id="standard-basic" label="Address 2 (Optional)"/>
                            </FormControl>
                        </Box>

                        <Grid container>
                            <Grid item lg={6}>
                                <Box mb={3} mr={3}>
                                    <FormControl fullWidth className={classes.margin}>
                                        <TextField color="secondary" id="standard-basic" label="State"/>
                                    </FormControl>
                                </Box>
                            </Grid>
                            <Grid item lg={6}>
                                <Box mb={3} mr={3}>
                                    <FormControl fullWidth className={classes.margin}>
                                        <TextField color="secondary" id="standard-basic" label="LandMark (Optional)"/>
                                    </FormControl>
                                </Box>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item>
                        <Box mt={5} ml={5}>
                            <Button
                                style={{marginRight: '20px'}}
                                size="small"
                                color="default"
                                onClick={hideForm}
                            >
                                cancel
                            </Button>
                            <Button
                                onClick={props.nextStep}
                                variant="contained"
                                size="small"
                                color="secondary"
                            >
                                Save and Deliver here
                            </Button>
                        </Box>
                    </Grid>
                </Grid>
                </div>
            }
        </React.Fragment>
    )
}
