import {SHOW_PRODUCT_DETAILS, HIDE_PRODUCT_DETAILS} from '../Actions/all-action.types';

const INIT_SHOW_PRODUCT_DETAILS = {
    showProduct: false,
    productData: null
};

const productDetailsReducer = (state = INIT_SHOW_PRODUCT_DETAILS, action: any) => {

    switch (action.type) {
        case SHOW_PRODUCT_DETAILS:
            return {
                ...state, showProduct: true, productData: action.data,
            };

        case HIDE_PRODUCT_DETAILS:
            return {
                ...state, showProduct: false, productData: action.data,
            };

        default: return state;
    }

};

export default productDetailsReducer;
