import React, {useState} from 'react';
import './App.css';
import Header from './components/layout/header/Header';
import {CssBaseline, Paper} from '@material-ui/core';
import 'fontsource-roboto';
import {createMuiTheme, ThemeProvider} from '@material-ui/core/styles';
import {blue, deepOrange, deepPurple, indigo, lightBlue, orange} from '@material-ui/core/colors';
import store from './redux/store';
import { BrowserRouter as Router } from "react-router-dom";

const darkColor = '#121a27';
// const light = '#'
function App(props:any) {
    const [darkState, setDarkState] = useState(true);
    const palletType = darkState ? "dark" : "light";
    const mainPrimaryColor = darkState ? darkColor : lightBlue[900];
    const mainSecondaryColor = darkState ? lightBlue[800] : deepOrange[900];
    const backgroundColor = darkState ? darkColor : '#fff';
    const darkTheme = createMuiTheme({
        palette: {
            type: palletType,
            primary: {
                light: '#757ce8',
                main: mainPrimaryColor,
                contrastText: '#ffffff',
            },
            secondary: {
                main: mainSecondaryColor,
                contrastText: '#ffffff',
            },
            background: {
                paper: backgroundColor,
            },
        },
    });
    const handleThemeChange = () => {
        const unsubscribe = store.subscribe(() => console.log(store.getState()))
        console.log(props.count)
        setDarkState(!darkState);
    };
    return (
    <div className="App">
        <Router>
            <ThemeProvider theme={darkTheme}>
                <CssBaseline />
            <Paper>
                <Header onChangeMode={handleThemeChange} />
            </Paper>
            </ThemeProvider>
        </Router>
    </div>
  );
}

export default App;

