import React, { PureComponent} from 'react';
import AppBar from '@material-ui/core/AppBar';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import Brightness4Icon from '@material-ui/icons/Brightness4';
import ListItemText from '@material-ui/core/ListItemText';
import MenuIcon from '@material-ui/icons/Menu';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import {withStyles} from '@material-ui/core/styles';
import SearchIcon from '@material-ui/icons/Search';
import {Badge, Box, InputBase, Menu, MenuItem} from '@material-ui/core';
import {headerStyle} from './Header.style';
import NavButtonComponent from './Nav-button.component';
import {navMenu} from '../../../_constant/navbar.constant';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import {AccountCircle} from '@material-ui/icons';
import HomeComponent from '../../home/Home.component';
import {Switch, Route, Link} from 'react-router-dom';
import ProductCategoryComponent from '../../product-category/Product-category.component';
import ProductDetailsComponent from '../../product-details/product-details.component';
import { withRouter } from 'react-router-dom';
import SignUpSteperComponent from '../../user/sign-up/Sign-up-steper.component';
import SignInComponent from '../../user/sign-in/User-sign-in.component';
import FooterComponent from '../footer/Footer.component';
import CartComponent from '../../cart/Cart.component';
import CheckOutStepperComponent from '../../checkout/Checkout-stepper.component';



class Header extends PureComponent<any, any> {
    state = {
        mobileOpen: false,
        userMenuDropDown: null
    }

    handleMenu = (event: React.MouseEvent<HTMLElement>) => {
        this.setState({
            userMenuDropDown :event.currentTarget
        })
    };
    handleClose = () => {
        this.setState({
            userMenuDropDown :null
        })
    };

    handleDrawerToggle = () => {
        this.setState((state: any, props: any) => ({
            mobileOpen: !state.mobileOpen
        }));
    };
    drawer = (
        <div>
            <div className={this.props.classes.toolbar}/>
            <Divider/>
            {navMenu.length > 0 ?
                navMenu.map((navItem, index) =>
                    <Box ml={2} mr={2}>
                        <List>
                            <ListItemText primary={navItem.menu}/>
                        </List>
                        <Divider/>
                    </Box>
                ): null}
        </div>
    );
    navigate = (path: any) => {
        this.props.history.push(path)
    }
    render() {
        const open = Boolean(this.state.userMenuDropDown);
        const container = window !== undefined ? () => window.document.body : undefined;
        return (
            <React.Fragment>
                <div className={this.props.classes.root}>
                    <AppBar position="fixed">
                        <Toolbar>
                            <Hidden only={['lg', 'md']}>
                                <IconButton
                                    edge="start"
                                    className={this.props.classes.menuButton}
                                    color="inherit"
                                    onClick={this.handleDrawerToggle}
                                    aria-label="open drawer"
                                >
                                    <MenuIcon/>
                                </IconButton>
                            </Hidden>
                            <Box style={{cursor: 'pointer'}} ml={2} mr={4}>
                                    <Typography onClick={() => this.navigate('/')} variant="h6" color="inherit">
                                        Flipkart
                                    </Typography>
                            </Box>
                            <Hidden only={['sm', 'xs']}>
                                {
                                    navMenu.length > 0 ?
                                        navMenu.map((navItem, index) =>
                                            <Box ml={2} mr={1}>
                                              <NavButtonComponent navItem={navItem}/>
                                            </Box>
                                        ) : null
                                }
                            </Hidden>
                            <Typography className={this.props.classes.title} variant="h6" noWrap>
                            </Typography>
                            <div className={this.props.classes.search}>
                                <div className={this.props.classes.searchIcon}>
                                    <SearchIcon/>
                                </div>
                                <InputBase
                                    placeholder="Search…"
                                    classes={{
                                        root: this.props.classes.inputRoot,
                                        input: this.props.classes.inputInput,
                                    }}
                                    inputProps={{'aria-label': 'search'}}
                                />
                            </div>

                            <IconButton
                                onClick={this.props.onChangeMode}
                                color="inherit">
                                    <Brightness4Icon />
                            </IconButton>

                            <div>
                                <IconButton
                                    aria-label="account of current user"
                                    aria-controls="menu-appbar"
                                    aria-haspopup="true"
                                    onClick={this.handleMenu}
                                    color="inherit"
                                >
                                    <AccountCircle />
                                </IconButton>
                                <Menu
                                    id="menu-appbar"
                                    anchorEl={this.state.userMenuDropDown}
                                    anchorOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    keepMounted
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    open={open}
                                    onClose={this.handleClose}
                                >
                                    <MenuItem onClick={() => { this.navigate('/login'); this.handleClose();}}>Login</MenuItem>
                                    <MenuItem onClick={this.handleClose}>My account</MenuItem>
                                    <MenuItem onClick={this.handleClose}>My order</MenuItem>
                                    <MenuItem
                                        onClick={() => { this.navigate('/create-account'); this.handleClose();}}>Sign Up</MenuItem>
                                </Menu>
                            </div>
                            <IconButton aria-label="show 17 new notifications" color="inherit">
                                <Badge badgeContent={17} onClick={() => this.navigate('/cart')} color="secondary">
                                    <ShoppingCartIcon />
                                </Badge>
                            </IconButton>
                        </Toolbar>
                    </AppBar>
                    <nav className={this.props.classes.drawer} aria-label="mailbox folders">
                        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
                        <Hidden smUp implementation="css">
                            <Drawer
                                container={container}
                                variant="temporary"
                                anchor={'left'}
                                open={this.state.mobileOpen}
                                onClose={this.handleDrawerToggle}
                                classes={{
                                    paper: this.props.classes.drawerPaper,
                                }}
                                ModalProps={{
                                    keepMounted: true, // Better open performance on mobile.
                                }}
                            >
                                {this.drawer}
                            </Drawer>
                        </Hidden>
                    </nav>
                    <main className={this.props.classes.content}>
                        <Switch>
                            {/*<Route path="/about">*/}

                            {/*</Route>*/}
                            <Route path={'/cart'} component={CartComponent} />
                            <Route path={'/checkout'} component={CheckOutStepperComponent} />
                            <Route path="/product/:id" component={ProductCategoryComponent} />
                            <Route path="/create-account" component={SignUpSteperComponent} />
                            <Route path="/login" component={SignInComponent} />
                            <Route path="/" component={HomeComponent} exact={true} />
                        </Switch>
                        <ProductDetailsComponent />
                        {/*<FooterComponent />*/}

                    </main>
                </div>
            </React.Fragment>
        );
    }
}

// @ts-ignore
export default withStyles(headerStyle, {withTheme: true})(withRouter(Header))

