import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import {Box} from '@material-ui/core';
import {navMenu} from '../../../_constant/navbar.constant';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: '100%',
            maxWidth: 360,
            backgroundColor: theme.palette.background.paper,
        },
        nested: {
            paddingLeft: theme.spacing(4),
        },
    }),
);

export default function CategoryListComponent() {
    const classes = useStyles();
    const [open, setOpen] = React.useState([0]);
    const handleClick = (index: any) => {
        if(open.includes(index)) {
            const filteredAry = open.filter(e => e !== index)
            setOpen(filteredAry);
        } else {
            const menu = [...open];
            menu.push(index);
            setOpen(menu);
        }
    };

    return (
        <React.Fragment>
            <Box mt={10}>
        <List
            component="nav"
            aria-labelledby="nested-list-subheader"
            className={classes.root}
        >
            {navMenu.length > 0 ?
                navMenu.map((item, index) =>
            <Box>
                <ListItem button onClick={() => handleClick(index)}>
                    <ListItemText primary={item.menu} />
                    {open.includes(index) ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Collapse in={open.includes(index)} timeout="auto" unmountOnExit>
                    {item.submenu.length > 0 ?
                        item.submenu.map((menuItem:any, index) =>
                    <List component="div" disablePadding>
                        <ListItem button className={classes.nested}>
                            <ListItemText primary={menuItem.title} />
                        </ListItem>
                    </List>
                    ): null}
                </Collapse>
            </Box>
                ): null}
        </List>
            </Box>
        </React.Fragment>
    );
}
