import React, {PureComponent} from 'react';
import {Box, Button, Container, Grid, Typography} from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import {withStyles} from '@material-ui/core/styles';
import {headerStyle} from '../layout/header/Header.style';
import {withRouter} from 'react-router-dom';
const styles = () => ({
    table: {
        minWidth: 650,
    },
});

function createData(name: any, calories: any, fat: any, carbs: any, protein: any) {
    return { name, calories, fat, carbs, protein };
}

const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
];

class CartComponent extends PureComponent<any, any> {
    navigate = (path: string) =>{
        this.props.history.push(path);
    }
    render() {
        return(
            <React.Fragment>
                <Box mt={7} pt={5} className={'full-height'}>
                    <Container maxWidth="lg">
                        <Grid container>
                            <Box mb={5}>
                                <Typography variant="h6" component="h5">
                                  My Shopping Cart
                                </Typography>
                            </Box>
                            <TableContainer component={Paper}>
                                <Table className={this.props.classes.table} aria-label="simple table">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell>Image</TableCell>
                                            <TableCell align="right">Name</TableCell>
                                            <TableCell align="right">Price</TableCell>
                                            <TableCell align="right">Quantity</TableCell>
                                            <TableCell align="right">Action</TableCell>
                                            <TableCell align="right">Total</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {rows.map((row) => (
                                            <TableRow key={row.name}>
                                                <TableCell component="th" scope="row">
                                                    {row.name}
                                                </TableCell>
                                                <TableCell align="right">{row.calories}</TableCell>
                                                <TableCell align="right">{row.fat}</TableCell>
                                                <TableCell align="right">{row.carbs}</TableCell>
                                                <TableCell align="right">{row.carbs}</TableCell>
                                                <TableCell align="right">{row.protein}</TableCell>
                                            </TableRow>
                                        ))}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                            <Grid item lg={12} style={{marginTop: '20px', textAlign: 'right'}}>
                                <Typography  align="right" variant="h6" component="h6">
                                   Total Cost: <span style={{marginLeft: '20px'}}>2000</span>
                                </Typography>
                                <Button onClick={() => this.navigate('/checkout')} style={{marginTop: '20px'}} variant="contained" size="large" color="secondary">
                                    Checkout
                                </Button>
                            </Grid>
                        </Grid>
                    </Container>
                </Box>
            </React.Fragment>
        );
    }
}

export default withStyles(styles())(withRouter(CartComponent))
