import React,{Component} from 'react';
import Carousel from 'react-multi-carousel';
import "react-multi-carousel/lib/styles.css";
import {Box, Grid,  Typography} from '@material-ui/core';
import {todayDeals} from '../../../../_constant/navbar.constant';
import TodayDealsComponent from './product-card/Product-card.component';


class ProductSliderComponent extends Component<any, any>{
    render() {
        const responsive = {
            desktop: {
                breakpoint: { max: 3000, min: 1024 },
                items: 4,
                paritialVisibilityGutter: 60
            },
            tablet: {
                breakpoint: { max: 1024, min: 464 },
                items: 2,
                paritialVisibilityGutter: 50
            },
            mobile: {
                breakpoint: { max: 464, min: 0 },
                items: 1,
                paritialVisibilityGutter: 30
            }
        };

        return (
            <React.Fragment>
                <Box  pb={3}>
                        <Grid container>
                            <Grid item lg={12}>
                                <Box color="primary" ml={3} p={2} fontWeight={500}>
                                    <Typography variant="h6" >
                                        {this.props.heading}
                                    </Typography>
                                </Box>
                            </Grid>
                            <Grid item lg={12} md={12} xs={12} sm={12}>
                                <Box ml={5} mr={5}>
                                    <Carousel
                                        ssr
                                        partialVisbile
                                        itemClass="image-item"
                                        responsive={responsive}
                                        swipeable={true}
                                        draggable={true}
                                        showDots={false}
                                        infinite={true}
                                        keyBoardControl={true}
                                    >
                                        {this.props.data.length > 0 ?
                                            this.props.data.map((item: any, index: any)=>
                                                <TodayDealsComponent product={item}  />
                                            ) : null}
                                    </Carousel>
                                </Box>
                            </Grid>
                        </Grid>
                </Box>
            </React.Fragment>
        );
    }
}

export default ProductSliderComponent
