import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import {Box, Button, Grid, Zoom} from '@material-ui/core';
import {connect} from 'react-redux';
import {showProductDetails} from '../../../../../redux/Actions/product-details.actions';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            maxWidth: 300,

        },
        media: {
            height: 0,
            paddingTop: '56.25%', // 16:9
        },
        title: {
          fontSize: '14px'
        },
        price: {
            fontSize: '14px',
        },
        expand: {
            transform: 'rotate(0deg)',
            marginLeft: 'auto',
            transition: theme.transitions.create('transform', {
                duration: theme.transitions.duration.shortest,
            }),
        },
        expandOpen: {
            transform: 'rotate(180deg)',
        },
        avatar: {
            backgroundColor: red[500],
        },
    }),
);

function ProductCardComponent(props: any) {
    const classes = useStyles();
    const product = props.product;
    return (
        <Box mr={2}>
        <Card className={classes.root}>
            <CardMedia
                style={{cursor: 'pointer'}}
                onClick={($event) => props.showProductDetails(product)}
                className={classes.media}
                image={product.img}
                title={product.name}
            />
            <Box mt={1} ml={1} style={{cursor: 'pointer'}} >
                <Typography
                    onClick={($event) => props.showProductDetails(product)}
                    className={"MuiTypography--heading " + classes.title}
                    variant={"h6"}
                    gutterBottom
                >
                    {product.name}
                </Typography>
            </Box>
            <CardActions>

                <Grid container>
                    <Grid item lg={9} md={9} sm={9} xs={9}>
                        <Button size="small" color="inherit" >
                            Add to Cart
                        </Button>
                    </Grid>
                    <Grid item lg={3} md={3} sm={3} xs={3}>
                    <Typography
                    className={"MuiTypography--heading " + classes.price}
                    variant={"h6"}
                    color="secondary"
                    gutterBottom
                >
                        ₹ {product.price}
                </Typography>
                    </Grid>
                </Grid>
            </CardActions>
        </Card>
        </Box>
    );
}
const mapStateToProps = (state: any) => {
    return {
        showProduct: state.productDetails.showProduct,
    }
}
const mapDispatchToProps = (dispatch: any) => {
    return {
        showProductDetails: (obj: string) => dispatch(showProductDetails(obj)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ProductCardComponent)
