import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import {Box} from '@material-ui/core';
import UserLoginInfoComponent from './steps/User-login-info.component';
import CheckOutAddressComponent from './steps/Checkout-address.component';
import CheckOutOrderSummaryComponent from './steps/Checkout-order-summary.component';
import CardFormComponent from './steps/payment-options/Card-form.component';
import CheckoutConfirmationComponent from './steps/Checkout-confirmation.component';
import FormControl from '@material-ui/core/FormControl';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: '100%',
        },
        button: {
            marginTop: theme.spacing(1),
            marginRight: theme.spacing(1),
        },
        actionsContainer: {
            marginBottom: theme.spacing(2),
        },
        resetContainer: {
            padding: theme.spacing(3),
        },
    }),
);

function getSteps() {
    return ['Login', 'Delivery Address', 'Order Summary', 'Payment Options', 'Confirmation'];
}



export default function CheckOutStepperComponent() {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(1);
    const steps = getSteps();
    const getStepContent = (step: number) => {
        switch (step) {
            case 0:
                return <UserLoginInfoComponent />;
            case 1:
                return <CheckOutAddressComponent nextStep={handleNext} />;
            case 2:
                return <CheckOutOrderSummaryComponent nextStep={handleNext} />;
            case 3:
                return <CardFormComponent  nextStep={handleNext}/>;
            case 4:
                return <CheckoutConfirmationComponent />
        }
    }
    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleReset = () => {
        setActiveStep(0);
    };

    return (
        <React.Fragment>
        <Box mt={5} pt={5} className={'full-height'}>
            <Box ml={4} mt={2}>
                <Typography variant="h6">
                    Checkout
                </Typography>
            </Box>
        <div className={classes.root}>
            <Stepper activeStep={activeStep} orientation="vertical">
                {steps.map((label, index) => (
                    <Step key={label}>
                        <StepLabel >{label}</StepLabel>
                        <StepContent>
                            <Box>{getStepContent(index)}</Box>
                            {/*<div className={classes.actionsContainer}>*/}
                            {/*    <div>*/}
                            {/*        <Button*/}
                            {/*            disabled={activeStep === 0}*/}
                            {/*            onClick={handleBack}*/}
                            {/*            className={classes.button}*/}
                            {/*        >*/}
                            {/*            Back*/}
                            {/*        </Button>*/}
                            {/*        <Button*/}
                            {/*            variant="contained"*/}
                            {/*            color="primary"*/}
                            {/*            onClick={handleNext}*/}
                            {/*            className={classes.button}*/}
                            {/*        >*/}
                            {/*            {activeStep === steps.length - 1 ? 'Finish' : 'Next'}*/}
                            {/*        </Button>*/}
                            {/*    </div>*/}
                            {/*</div>*/}
                        </StepContent>
                    </Step>
                ))}
            </Stepper>
            {activeStep === steps.length && (
                <Paper square elevation={0} className={classes.resetContainer}>
                    <Typography>All steps completed - you&apos;re finished</Typography>
                    <Button onClick={handleReset} className={classes.button}>
                        Reset
                    </Button>
                </Paper>
            )}
        </div>
</Box>
        </React.Fragment>
    );
}
