import React, {Component} from 'react';
import SingleSliderComponent from './slider/Single-slider.component';
import CouponSliderComponent from './slider/coupon-slider/Coupon-slider.component';
import ShopByCategorySliderComponent from './slider/shop-by-category-slider/Shop-by-category-slider.component';
import {connect} from 'react-redux';
import {decreaseCounter, increaseCounter} from '../../redux/Actions/counter.actions';
import ProductDetailsComponent from '../product-details/product-details.component';
import ProductSliderComponent from './slider/product-slider/Product-slider.component';
import {todayDeals} from '../../_constant/navbar.constant';
import LoadingComponent from '../Loading/Loading.component';

class HomeComponent extends Component<any, any> {
    state= {
        loading: true,
    }
    componentDidMount() {
        setTimeout(()=> {
            this.setState({
                loading: false
            })
        }, 4000)

    }

    render() {
        const { loading } = this.state;
        return (
            <React.Fragment>
                {loading ? <LoadingComponent /> : null }
                <SingleSliderComponent />
                <CouponSliderComponent />
                <ProductSliderComponent heading="Today's Deals" data={todayDeals} />
                <ShopByCategorySliderComponent />
                <ProductSliderComponent heading="Fruits & Vegetables" data={todayDeals} />
                <ProductSliderComponent heading="Cooking" data={todayDeals} />
                <ProductSliderComponent heading="Beauty & Health" data={todayDeals} />
            </React.Fragment>
        );
    }
}
const mapStateToProps = (state: any) => {
    return {
        count: state.counter.count,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        increaseCounter: () => dispatch(increaseCounter()),

        decreaseCounter: () => dispatch(decreaseCounter()),
    }
}
// const unsubscribe = store.subscribe(handleThemeChange)
export default connect(mapStateToProps, mapDispatchToProps)(HomeComponent)
