import React, {Component} from 'react';
import Cards from 'react-credit-cards';
import 'react-credit-cards/es/styles-compiled.css';
import {Box, Button, FormControl, TextField} from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import PaymentMethodsComponent from './Stripe-payment.component';

export default class CardFormComponent extends Component<any, any> {
    state = {
        cvc: '',
        expiry: '',
        focus: '',
        name: '',
        number: '',
    };

    handleInputFocus = (e: any) => {
        console.log(typeof e.target.name)
        this.setState({focus: e.target.name});
    }

    handleInputChange = (e: any) => {
        const {name, value} = e.target;

        this.setState({[name]: value});
    }

    render() {
        const focus = this.state.focus;
        return (
            <React.Fragment>
                {/*<StripePaymentComponent/>*/}
                <PaymentMethodsComponent />
                <Grid container spacing={10}>
                    <Grid item lg={6}>
                        <Box mt={3}>
                            <Typography variant="h6" gutterBottom>
                                Payment method
                            </Typography>
                        </Box>
                        <Grid container spacing={3}>
                            <Grid item xs={12} md={6}>
                                <TextField
                                    required
                                    id="cardName"
                                    name="name" color="secondary" onChange={this.handleInputChange}
                                    onFocus={this.handleInputFocus}
                                    label="Name on card"
                                    fullWidth autoComplete="cc-name"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField
                                    required
                                    id="cardNumber"
                                    name="number" color="secondary" onChange={this.handleInputChange}
                                    onFocus={this.handleInputFocus}
                                    label="Card number"
                                    fullWidth
                                    autoComplete="cc-number"
                                />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField required id="expiry"
                                           name="expiry" color="secondary" onChange={this.handleInputChange}
                                           onFocus={this.handleInputFocus}
                                           label="Expiry date" fullWidth autoComplete="cc-exp"/>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField
                                    required
                                    id="cvv"
                                    label="CVV"
                                    name="cvc"
                                    onChange={this.handleInputChange}
                                    onFocus={this.handleInputFocus}
                                    helperText="Last three digits on signature strip"
                                    fullWidth
                                    autoComplete="cc-csc"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <FormControlLabel
                                    control={<Checkbox color="secondary" name="saveCard" value="yes"/>}
                                    label="Remember credit card details for next time"
                                />
                            </Grid>
                            <Box ml={5}>
                                <Button
                                    onClick={this.props.nextStep}
                                    variant="contained"
                                    size="large"
                                    color="secondary"
                                >
                                    Pay Now
                                </Button>
                            </Box>
                        </Grid>
                    </Grid>


                    <Grid item lg={3}>
                        <div id="PaymentForm">
                            <Cards
                                cvc={this.state.cvc}
                                focused={focus === 'number' ? 'number'
                                    : focus === 'cvc' ? 'cvc'
                                        : focus === 'expiry' ? 'expiry'
                                            : 'name'}
                                expiry={this.state.expiry}
                                name={this.state.name}
                                number={this.state.number}
                            />
                        </div>
                    </Grid>

                </Grid>
            </React.Fragment>

        );
    }
}
